@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

#root {
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 100%;
  height: 100%;
}

/* Position and sizing of burger button */
.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    right: 36px;
    top: 36px;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: black;
    outline: 2px solid white;
  }
  
  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background: white;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: white;
  }
  
  /*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
  .bm-menu-wrap {
    position: fixed;
    height: 100%;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: black;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
    overflow: hidden !important;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: white;
    padding: 0.8em;
  }
  
  /* Individual item */
  .bm-item {
    display: inline-block;
    color: white;
    text-decoration: none;
    font-size: 2rem;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }
.about {
    /* background: black; */
    background-image: url(/static/media/background-dark.d08f48ea.jpg);
    background-size: cover;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.about .content {
    max-width: 700px;
    padding: 50px;
    text-align: justify;
    color: white;
}

.about .content .images {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: center;
    margin-bottom: 50px;
}

.about .content .images .profile {
    max-width: 50%;
}

.about .content .images .profile img {
    max-width: 100%;
    max-height: 300px;
    padding: 20px;
    box-sizing: border-box;
    border-radius: 50px;
}

.about .content .images .profile h5 {
    text-align: center;
    margin: 0;
}
.background {
    display: flex;
    flex-direction: column;
    background: black;
    color: white;
    padding: 50px 100px;
    max-width: 1200px;
}

@media (max-width: 600px) {
    .background {
        padding: 50px 50px;
    }
}

.background #figure {
    width: 100%;
}

.background #source {
    font-style: italic;
    text-align: center;
    padding: 5px;
}

.background a {
    color: white;
}
.home {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: black;
}

.home .header {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Staatliches', cursive;
    font-size: 4rem;
    margin: 0;
    padding: 10px;
    text-shadow: 0 0 2px white, 0 0 2px white, 0 0 2px white, 0 0 2px white;
    box-sizing: border-box;
    background-image: url(/static/media/background.8f23cddb.jpg);
    background-size: cover;
}

.home .header h1 {
    margin: 0;
    max-width: 700px;
}

.home .mission {
    background: yellow;
    padding: 50px 100px;
}

.home .mission p {
    font-size: 1.25rem;
}

@media (max-width: 600px) {
    .home .header {
        font-family: 'Staatliches', cursive;
        font-size: 3.5rem;
        margin: 0;
        padding: 10px;
    }

    .home .mission {
        padding: 10px 20px;
    }
}
.footer {
    text-align: center;
    font-size: 1.25rem;
    padding: 30px;
    color: white;
    background-image: url(/static/media/background.8f23cddb.jpg);
    background-size: cover;
}

.footer a {
    color: white;
    text-decoration: none;
}

.story {
    width: 100%;
    flex-direction: row;
    box-sizing: border-box;
    margin: 20px 0px;
    color: white;
    justify-content: flex-end;
    display: flex;
    padding: 0px 50px;
}

.story:nth-child(odd) {
    justify-content: flex-start;
}

.story .wrapper {
    max-width: 700px;
    min-width: 300px;
    text-align: justify;
    padding: 20px;
}

.story .wrapper .bolded {
    font-style: italic;
    text-align: left;
}

.story .wrapper a {
    color: white;
}

@media (max-width: 600px) {
    .story {
        padding: 0;
        margin: 30px 0px;
    }
}


.stories {
    background-image: url(/static/media/background-dark.d08f48ea.jpg);
    background-size: cover;
    background-attachment: fixed;
    padding-top: 50px;
    padding-bottom: 50px;
}

.stories h1 {
    text-align: center;
    color: white;
}

.stories .content {
    max-width: 1200px;
    margin: auto;
}

.resources {
    background-image: url(/static/media/background.8f23cddb.jpg);
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 100px;
    color: white;
}

.resources .content {
    background: black;
    max-width: 700px;
    border-radius: 10px;
    padding: 25px 50px;
}

.resources .content a {
    color: white;
}
